export function buttonUtils({ element }) {
    const { settings } = element;
    const fullWidthClass = settings.fullWidthEnabled === 'yes' ? 'xb-button--full' : 'xb-button--fit';
    const actionEnabled = settings.action?.actionType !== 'no';
    const tagName = actionEnabled ? 'a' : 'button';
    const classLink = actionEnabled ? 'xb-link' : '';
    return {
        fullWidthClass,
        tagName,
        classLink,
        actionEnabled,
    };
}
